import React, { useState } from 'react'
import 'bootstrap/dist/css/bootstrap.min.css'
import '../../style.css'
import './style.css'
import { Link } from 'react-router-dom'
import { Container, Row, Col, Card, Form, Button } from 'react-bootstrap'
import { message } from 'antd'

const Index = () => {
  const [activeTab, setActiveTab] = useState('Home')
  const [validatedHome, setValidatedHome] = useState(false)
  const [validatedLogo, setValidatedLogo] = useState(false)
  const [validatedAbout, setValidatedAbout] = useState(false)
  const [businessName, setBusinessName] = useState('')
  const [contentSection, setContentSection] = useState('')
  const [contactUsHeadingHome, setContactUsHeadingHome] = useState('')
  const [logo, setLogo] = useState('')
  const [introduction, setIntroduction] = useState('')
  const [aboutContent, setAboutContent] = useState('')
  const [selfPolicies, setSelfPolicies] = useState('')

  const handleTabClick = (tab) => {
    setActiveTab(tab)
  }

  const handleHomeChange = (event) => {
    const form = event.currentTarget
    if (form.checkValidity() === false) {
      event.preventDefault()
      event.stopPropagation()
    }
    setValidatedHome(true)
    event.preventDefault()
  }

  const handleLogoChange = (event) => {
    console.log('logo', logo)
    console.log('submtted')
    const form = event.currentTarget
    if (form.checkValidity() === false) {
      event.preventDefault()
      event.stopPropagation()
    }
    if (logo === '') {
      message.error('Please select a logo')
      event.preventDefault()
      event.stopPropagation()
      return
    } else {
      //make an api call for change
      message.success('Logo updated Successfully')
    }
    setValidatedLogo(true)
    event.preventDefault()
  }

  const handleAboutUsChange = (event) => {
    console.log('logo', logo)
    console.log('submtted')
    const form = event.currentTarget
    if (form.checkValidity() === false) {
      event.preventDefault()
      event.stopPropagation()
    }
    //make an api call for change
    message.success('Logo updated Successfully')
    setValidatedAbout(true)
    event.preventDefault()
  }

  const logoPlaceHolder = {
    width: '250px',
    height: '250px',
    objectFit: 'contain',
    transform: 'scale(0.7)',
  }

  return (
    <div className="container">
      <ul className="nav nav-tabs ms-3">
        <li className="nav-item">
          <Link
            className={`nav-link myTab text-black ${activeTab === 'Home' ? 'active' : ''}`}
            onClick={() => handleTabClick('Home')}
            to="#"
          >
            Home
          </Link>
        </li>
        <li className="nav-item">
          <Link
            className={`nav-link myTab text-black ${activeTab === 'Logo' ? 'active' : ''}`}
            onClick={() => handleTabClick('Logo')}
            to="#"
          >
            Logo
          </Link>
        </li>
        <li className="nav-item">
          <Link
            className={`nav-link myTab text-black ${activeTab === 'About Us' ? 'active ' : ''}`}
            onClick={() => handleTabClick('About Us')}
            to="#"
          >
            About Us
          </Link>
        </li>
      </ul>
      <div className="tab-content">
        {activeTab === 'Home' && (
          <div className="tab-pane active" id="home-content">
            <Form
              noValidate
              validated={validatedHome}
              onSubmit={handleHomeChange}
              autoComplete="off"
            >
              <Container className="border border-black rounded-4">
                <Row className="h3 ms-3 mt-2">Home Settings</Row>
                <Card className="ms-3 my-3">
                  <Card.Header>
                    <b>Home Top Section</b>
                  </Card.Header>
                  <Card.Body>
                    <Row className="mb-3">
                      <div className="">
                        <Form.Group
                          as={Col}
                          className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                          controlId="formName"
                        >
                          <Form.Label>BusinessName</Form.Label>
                          <Form.Control
                            type="text"
                            placeholder="Enter Business Name"
                            onChange={(e) => {
                              setBusinessName(e.target.value)
                            }}
                            value={businessName}
                            required
                          />
                        </Form.Group>
                      </div>
                    </Row>
                    <Row className="mb-3">
                      <div className="">
                        <Form.Group as={Col} controlId="formName">
                          <Form.Label>Content Section</Form.Label>
                          <Form.Control
                            as="textarea"
                            rows={3}
                            placeholder="Enter Content"
                            onChange={(e) => {
                              setContentSection(e.target.value)
                            }}
                            value={contentSection}
                            required
                          />
                        </Form.Group>
                      </div>
                    </Row>
                    <Row className="mb-3">
                      <div className="d-flex justify-content-center align-items-center align-items-md-end  justify-content-md-between   flex-column flex-md-row">
                        <Form.Group
                          as={Col}
                          className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                          controlId="formName"
                        >
                          <Form.Label>Contact Us Heading</Form.Label>
                          <Form.Control
                            type="text"
                            placeholder="Enter Contact Us Heading"
                            onChange={(e) => {
                              setContactUsHeadingHome(e.target.value)
                            }}
                            value={contactUsHeadingHome}
                            required
                          />
                        </Form.Group>
                        <div className="mt-3 mt-md-0">
                          <Button type="submit">Update Changes</Button>
                        </div>
                      </div>
                    </Row>
                  </Card.Body>
                </Card>
              </Container>
            </Form>
          </div>
        )}
        {activeTab === 'Logo' && (
          <div className="tab-pane active mb-3" id="logo-content">
            <Form
              noValidate
              validated={validatedLogo}
              onSubmit={handleLogoChange}
              autoComplete="off"
            >
              <Container className="border border-black rounded-4">
                <Row className="h3 ms-3 mt-2">Logo</Row>
                <Card className="ms-3 my-3">
                  <Card.Header>
                    <b>Set logo</b>
                  </Card.Header>
                  <Card.Body>
                    <Row className="mb-3">
                      <div className="d-flex justify-content-center align-items-center align-items-md-end  justify-content-md-between   flex-column flex-md-row">
                        <Form.Group as={Col} controlId="formName" className="col-6">
                          <Form.Label>Choose Logo</Form.Label>
                          <Form.Control
                            type="file"
                            onChange={(e) => {
                              setLogo(e.target.value)
                            }}
                            value={logo}
                            required
                          />
                        </Form.Group>
                        <div className="mt-3 mt-md-0">
                          <Button type="submit">Update Logo</Button>
                        </div>
                      </div>
                    </Row>
                  </Card.Body>
                </Card>
                <Card className="ms-3 my-3">
                  <Card.Header>Current Logo</Card.Header>
                  <Card.Body>
                    <div
                      className={`${logoPlaceHolder}`}
                      style={{
                        height: '200px',
                        width: '200px',
                        backgroundColor: '#0e1726',
                        objectFit: 'contain',
                      }}
                    >
                      <div className=" w-100 h-100" style={{}}></div>
                    </div>
                  </Card.Body>
                </Card>
              </Container>
            </Form>
          </div>
        )}
        {activeTab === 'About Us' && (
          <div className="tab-pane active mb-3" id="about-us-content">
          <Form
              noValidate
              validated={validatedAbout}
              onSubmit={handleAboutUsChange}
              autoComplete="off"
            >
              <Container className="border border-black rounded-4">
                <Row className="h3 ms-3 mt-2">Home Settings</Row>
                <Card className="ms-3 my-3">
                  <Card.Header>
                    <b>About us Content Update</b>
                  </Card.Header>
                  <Card.Body>
                    <Row className="mb-3">
                      <div className="">
                        <Form.Group
                          as={Col}
                          className="col-12"
                          controlId="formName"
                        >
                          <Form.Label>Introduction</Form.Label>
                          <Form.Control
                            type="text"
                            placeholder="Enter Introduction"
                            onChange={(e) => {
                              setIntroduction(e.target.value)
                            }}
                            value={introduction}
                            required
                          />
                        </Form.Group>
                      </div>
                    </Row>
                    <Row className="mb-3">
                      <div className="">
                        <Form.Group as={Col} controlId="formName">
                          <Form.Label>Content Section</Form.Label>
                          <Form.Control
                            as="textarea"
                            rows={3}
                            placeholder="Enter Content"
                            onChange={(e) => {
                              setAboutContent(e.target.value)
                            }}
                            value={aboutContent}
                            required
                          />
                        </Form.Group>
                      </div>
                    </Row>
                    <Row className="mb-3">
                      <div className="d-flex justify-content-center align-items-center align-items-md-end  justify-content-md-between   flex-column flex-md-row">
                        <Form.Group
                          as={Col}
                          className="col-sm-12 col-lg-8 col-xs-12 col-md-8 col-12"
                          controlId="formName"
                        >
                          <Form.Label>Self Policies <span className='text-danger'>*Note write your own business policy if required</span></Form.Label>
                          <Form.Control
                          as={"textarea"}
                          rows={3}
                            placeholder="Enter your custom policies"
                            onChange={(e) => {
                              setSelfPolicies(e.target.value)
                            }}
                            value={selfPolicies}
                            required
                          />
                        </Form.Group>
                        <div className="mt-3 mt-md-0">
                          <Button type="submit">Update Changes</Button>
                        </div>
                      </div>
                    </Row>
                  </Card.Body>
                </Card>
              </Container>
            </Form>
          </div>
        )}
      </div>
    </div>
  )
}

export default Index

